.app__footer {
 width: 100%;
 position: relative;
 z-index: 1;

 display: flex;
 align-items: center;
 flex-direction: column;

 background: #004B49;
 padding-top: 0;
}

.app__footer-links {
 
 width: 100%;
 display: flex;
 align-items: center;
 justify-content: space-between;

}


.email {
  font-family: var(--font-alt);
  color: var(--color-white);
  font-weight: 400;
  letter-spacing: 0.04em;
  line-height: 28px;
  font-size: 16px;
}

.app__footer-links_contacts,
.app__footer-links_logo,
.app__footer-links_work {
 
 margin: 1rem;
 text-align: center;
}

.app__footer-links_logo {
 width: 100%;
}

.app__footer-links_logo img:first-child {
 width: 210px;
 margin-bottom: 0.75rem;
}

.app__footer-links_icons {
 margin-top: 0.5rem;
}

.app__footer-links_icons svg {
 color: var(--color-white);
 margin: 0.5rem;
 font-size: 24px;
 cursor: pointer;
}

.app__footer-links_icons svg:hover {
 color: var(--color-golden);
}

.app__footer-headtext {
 font-family: var(--font-base);
 color: var(--color-white);
 font-weight: 400;
 letter-spacing: 0.04em;
 text-transform: capitalize;
 font-size: 32px;
 line-height: 41.6px;
 margin-bottom: 1rem;

}

.foooter__copyright {
 
 margin-top: 3rem;
}

@media screen and (max-width: 2000px) {
 .app__footer-headtext {
  font-size: 51px;
  line-height: 61px;
 }
}

@media screen and (max-width: 1150px) {
 .app__footer-links {
  align-items: center;
  flex-direction: column;
  padding: 0;
 }

 .app__footer-links_contacts,
.app__footer-links_logo,
.app__footer-links_work {
 
 margin: 2rem 0;
 width: 100%;

}

}

@media screen and (max-width: 650px) {
 .app__footer {
  padding: 0 0 2rem 0;
 }
}

@media screen and (max-width: 350px) {
 .app__footer-links_logo img:first-child {
  width: 80%;
 }
}